import sha1 from "sha1"
import echarts from 'echarts'
import "echarts/lib/component/tooltip"; //按需加载相关的包
import "echarts/lib/chart/pie";
import "echarts/lib/chart/line";
// import 'echarts/lib/component/tooltip'
// require('echarts/lib/component/tooltip');
export default {
	data() {
		return {
			look_worth:[],
			myChart:{},
			year_million:[],
			looks:[],
			treasure_data:{}
		}
	},
	mounted(){
		this.initChartData()
	},
	methods: {
		handle(){
			return true
		},
		initChartData(){
			let params = {
				// now_time	是	string	签名时间
				// sign	是	string	签名
				now_time:this.now_time(),
				limit: 4
			}
			let twoparams = this.objKeySort(params)
			let threeparams = this.stringify(twoparams)
			let sign = sha1(this.decrypt(this.key)+threeparams).toUpperCase()
			params.sign = sign
			this.uniRequest.post('/v1/look-worth.html',params).then(res=>{
				console.log(res)
				this.look_worth = res.data.data.look_worth
				this.year_million = res.data.data.year_million
				this.looks = res.data.data.looks
				this.treasure_data = res.data.data.treasure_data
				if(res.data.status == 1){
					console.log(res)
					// this.myChart = echarts.init(document.getElementById('Chart'));
					// app.title = '多 Y 轴示例';
					echarts.init(document.getElementById('Chart')).setOption({
						xAxis: {
							axisLine:{                 //设置X坐标的样式，这里设置的是白色
							
								lineStyle:{
									color:'#B8B8B8',
									fontSize: '24upx'
								}
							},
							// data: [],
							// data: res.data.data.categories
						},
						grid: {
							 left: '3%',   //图表距边框的距离
							 right: '2%',
							 bottom: '3%',
							 top: '4%',
							 containLabel: true
						},
						yAxis: [{
								axisLine:{                 //设置X坐标的样式，这里设置的是白色
									lineStyle:{
										color:'#B8B8B8',
										fontSize: '24upx'
									}
								},
							}
						],
						tooltip:{
						 	trigger: 'item',
						 	axisPointer: {
								type: 'cross'
							},
						},
						series: [{
							// 根据名字对应到相应的系列
							name: 'total',
							type: 'line',
							data: res.data.data.treasure_data.link,
							itemStyle: {
								normal: {
									color:'#F03644',
									label: {
									color:'#000', //字的颜色
									borderColor: '#A618FF',  //拐点边框颜色
									// show: true  //设置这个就会把值显示在图表上
									},
									lineStyle:{
										color:'#F03644'
									}
								}
							},
							label:{
								show:true
							},
							// hoverAnimation: true
						}]
					});
				}
			})
		},
	}
}
